:root,
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* background: #736e6e; */
  background: #3a74ff;
  color: #fff;
  font-family: "Verdana", sans-serif;
  --header-color: #2c3e50;
}

.header-container .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background: var(--header-color);
  -webkit-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
}

.header-container .header .header-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 20px;
  background: inherit;
  height: 100%;
}

.header-container .header .header-div h1,
.header-container .header .header-div i {
  margin-left: 20px;
  background: inherit;
}

.header-container .header nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: inherit;
  margin-right: 10px;
}

.header-container .header nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: inherit;
}

.header-container .header nav ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  height: 80%;
  background: inherit;
  margin-right: 10px;
}

.header-container .header nav ul li button {
  list-style: none;
  background: inherit;
  border: none;
  font-size: inherit;
  height: 100%;
  padding: 0 10px 0 10px;
  border: 1px solid var(--header-color);
  width: 100px;
  font-size: 20px;
}

.header-container .header ul li button:hover {
  /*background: rgb(14, 29, 63);
  border-radius: 15px;*/
  color: #3a74ff;
}

.header-container .popup {
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(128, 127, 127, 0.5);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.header-container .popup .create-post {
  margin-top: 30vh;
  width: 50vw;
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background: var(--header-color);
  color: var(--header-color);
  padding: 20px;
  border: 1px solid var(--header-color);
  border-radius: 10px;
}


.header-container .popup .create-post form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.header-container .popup .create-post input {
  width: 100%;
}


.header-container .popup .create-post h3,
.header-container .popup .create-post form,
.header-container .popup .create-post form input {
  background: var(--header-color);
}

.feed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}